import Vue from "vue";
import VueRouter from "vue-router";
// import store from "../store/store.js";

import Dashboard from "../components/Dashboard.vue";
import Roles from "../components/RolesComponent.vue";
import Users from "../components/UserComponent.vue";
import Projects from "../components/ProjectComponent.vue";
import Contractors from "../components/ContractorsComponent.vue";
import Master from "../components/MasterComponent.vue";
import MasterIndex from "../components/MasterIndexComponent.vue";
import MasterSolo from "../components/MasterSolo.vue";

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        name: "Dashboard",
        component: Dashboard,
        beforeEnter(to, from, next) {
            // store.state.isLoading = true;
            next();
        }
    },
    {
        path: "/roles",
        name: "Roles",
        component: Roles,
        beforeEnter(to, from, next) {
            // store.state.isLoading = true;
            next();
        }
    },
    {
        path: "/users",
        name: "Users",
        component: Users,
        beforeEnter(to, from, next) {
            // store.state.isLoading = true;
            next();
        }
    },
    {
        path: "/projects",
        name: "Projects",
        component: Projects,
        beforeEnter(to, from, next) {
            // store.state.isLoading = true;
            next();
        }
    },
    {
        path: "/contractors",
        name: "Contractors",
        component: Contractors,
        beforeEnter(to, from, next) {
            // store.state.isLoading = true;
            next();
        }
    },
    {
        path: "/master",

        component: Master,
        children: [
            { path: "/", component: MasterIndex, name: "MasterIndex" },
            { path: ":id", component: MasterSolo, name: "MasterSolo" }
        ]
    }
];

export default new VueRouter({ routes });
